import React from "react";
import { makeStyles } from "@mui/styles";
import { Email, LinkedIn, GitHub } from "@mui/icons-material";

const useStyles = makeStyles({
  container: {
    fontFamily: "monospace",
    color: "white",
    display: "flex",
    flexDirection: "column",
    gap: "10px",
    marginTop: "10px",
  },
  row: {
    display: "flex",
    alignItems: "center",
    gap: "10px",
  },
  label: {
    width: "120px",
    display: "flex",
    alignItems: "center",
    gap: "5px",
  },
  link: {
    color: "#7da6ff",
    textDecoration: "underline",
    cursor: "pointer",
  },
});

const socialLinks = [
  {
    label: "Email",
    icon: <Email fontSize="small" />,
    link: "mailto:hireayushkr@gmail.com",
    text: "hireayushkr@gmail.com",
  },
  {
    label: "LinkedIn",
    icon: <LinkedIn fontSize="small" />,
    link: "https://www.linkedin.com/in/ayush-kumar-2a1419131/",
    text: "linkedin/ayush-kumar-2a1419131/",
  },
  {
    label: "GitHub",
    icon: <GitHub fontSize="small" />,
    link: "https://github.com/ayush-ktr",
    text: "github.com/ayush-ktr",
  },
];

const SocialLinks = () => {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      <div>Hi there, please find me here</div>
      {socialLinks.map(({ label, icon, link, text }) => (
        <div className={classes.row} key={label}>
          <div className={classes.label}>
            {icon} {label}
          </div>
          <a
            href={link}
            target="_blank"
            rel="noopener noreferrer"
            className={classes.link}
          >
            {text}
          </a>
        </div>
      ))}
    </div>
  );
};

export default SocialLinks;
