import { call, put, all, takeLatest } from "redux-saga/effects";
import {
  fetchProjectsStart,
  fetchProjectsSuccess,
  fetchProjectsFailure,
} from "../slices/projectSlice";

// API call to fetch projects
const fetchProjectsApi = async () => {
  const response = await fetch("https://api.github.com/users/ayush-ktr/repos");
  if (!response.ok) {
    throw new Error("Failed to fetch projects");
  }
  return response.json();
};

// API call to fetch languages for a specific repo
const fetchLanguagesApi = async (languagesUrl) => {
  const response = await fetch(languagesUrl);
  if (!response.ok) {
    throw new Error("Failed to fetch languages");
  }
  return response.json();
};

// Worker saga to fetch languages for a single repo
function* fetchRepoWithLanguages(repo) {
  try {
    const languagesUrl = `${repo.url}/languages`;
    const languagesResponse = yield call(fetchLanguagesApi, languagesUrl);

    // Extract language names (keys) from the response
    const languages = Object.keys(languagesResponse);

    return {
      url: repo.url,
      htmlUrl: repo.html_url,
      name: repo.name,
      description: repo.description,
      languages: languages,
    };
  } catch (error) {
    // If language fetch fails, return repo without languages
    return {
      url: repo.url,
      htmlUrl: repo.html_url,
      name: repo.name,
      description: repo.description,
      languages: [],
    };
  }
}

// Main worker saga
function* fetchProjects() {
  try {
    const repos = yield call(fetchProjectsApi);

    // Fetch languages for all repos in parallel
    const projectsWithLanguages = yield all(
      repos.map((repo) => call(fetchRepoWithLanguages, repo))
    );

    yield put(fetchProjectsSuccess(projectsWithLanguages));
  } catch (error) {
    yield put(fetchProjectsFailure(error.message));
  }
}

// Watcher saga
export default function* projectSaga() {
  yield takeLatest(fetchProjectsStart.type, fetchProjects);
}
