import React from "react";
import { makeStyles } from "@mui/styles";
import clsx from "clsx";

const useStyles = makeStyles({
  codeContainer: {
    color: "#cdd6f4",
    fontFamily: "monospace",
    fontSize: "14px",
    whiteSpace: "pre-wrap",
    borderRadius: "8px",
    lineHeight: "1.6",
    width: "100%",
    height: "100%",
    maxWidth: "100%",
    maxHeight: "100%",
    wordWrap: "break-word",
    overflowWrap: "break-word",
    paddingTop: "0.5rem",
  },
  keyword: {
    color: "#cba6f7",
  },
  string: {
    color: "#a6e3a1",
  },
  comment: {
    color: "#6c7086",
  },
  property: {
    color: "#f38ba8",
  },
  number: {
    color: "#fab387",
  },
  punctuation: {
    color: "#cdd6f4",
  },
  white: {
    color: "#ffffff",
  },
  skillsContainer: {
    whiteSpace: "pre-wrap",
    wordWrap: "break-word",
    overflowWrap: "break-word",
  },
  methodSpacing: {
    marginBottom: "1rem", // Add space after each method
  },
  constructorEnd: {
    marginBottom: "1.5rem", // Extra space after constructor
  },
  methodContent: {
    paddingLeft: "25px", // Extra indentation for method content
  },
});

const About = () => {
  const classes = useStyles();

  return (
    <div className={classes.codeContainer}>
      <div>
        <span className={classes.keyword}>class</span>{" "}
        <span className={classes.white}>Ayush Kumar</span> {"{"}
      </div>
      <div className={classes.methodSpacing} />
      <div>
        <span className={classes.punctuation}> </span>
        <span className={classes.keyword}>constructor</span>() {"{"}
      </div>
      <div className={classes.methodContent}>
        <span className={classes.property}>this.name</span> ={" "}
        <span className={classes.string}>'Ayush Kumar'</span>
      </div>
      <div className={classes.methodContent}>
        <span className={classes.property}>this.dayOfBirthTimestamp</span> ={" "}
        <span className={classes.number}>933273000</span>
      </div>
      <div className={classes.methodContent}>
        <span className={classes.property}>this.email</span> ={" "}
        <span className={classes.string}>'hireayushkr@gmail.com'</span>
      </div>
      <div className={classes.constructorEnd}> {"}"}</div>

      <div>
        <span className={classes.punctuation}> </span>
        <span className={classes.keyword}>workExperience</span>() {"{"}
      </div>
      <div className={classes.methodContent}>
        <span className={classes.keyword}>return</span> [
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2023-Current' : 'Software Development Engineer III at Highradius'{" "}
        {"}"},
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2022-2023' : 'Software Development Engineer II at Highradius'{" "}
        {"}"},
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2020-2021' : 'Software Development Engineer I at Highradius'{" "}
        {"}"},
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2019-2020' : 'Software Developer Intern at Highradius' {"}"}
      </div>
      <div className={clsx(classes.methodContent, classes.methodSpacing)}>
        ]
      </div>
      <div className={classes.methodSpacing}>
        <span className={classes.punctuation}> </span>
        {"}"}
      </div>

      <div>
        <span className={classes.punctuation}> </span>
        <span className={classes.keyword}>education</span>() {"{"}
      </div>
      <div className={classes.methodContent}>
        <span className={classes.keyword}>return</span> [
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2016-2020': 'Bachelor of Technology - Information Technology at
        KIIT University' {"}"},
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2014-2016': 'Intermediate - GGPS Bokaro' {"}"}
      </div>
      <div className={classes.methodContent} style={{ paddingLeft: "50px" }}>
        {"{"} '2006-2014': 'Matriculate - SBP Katihar' {"}"}
      </div>
      <div className={clsx(classes.methodContent, classes.methodSpacing)}>
        ]
      </div>
      <div className={classes.methodSpacing}>
        <span className={classes.punctuation}> </span>
        {"}"}
      </div>

      <div>
        <span className={classes.punctuation}> </span>
        <span className={classes.keyword}>skills</span>() {"{"}
      </div>
      <div className={classes.methodContent}>
        <span className={classes.keyword}>return</span> {"{"}
      </div>
      <div className={clsx(classes.skillsContainer, classes.methodContent)}>
        {"      "}
        <span className={classes.property}>languages</span>: [
        <br />
        {"        "}
        'Java', 'Javascript', 'SQL', 'C', 'C++', 'Python', 'HTML', 'CSS'
        <br />
        {"      "}
        ],
        <br />
        {"      "}
        <span className={classes.property}>toolsAndTechnologies</span>: [
        <br />
        {"        "}
        'Spring(DI, AOP, Integration, MVC)', 'Spring Boot', 'Hibernate',
        'Struts',
        <br />
        {"        "}
        'Servlets', 'GIT/GitHub', 'AWS(SES, SNS, SQS, Pinpoint, S3, EC2, Open
        Search)',
        <br />
        {"        "}
        'Linux', 'Microservices', 'JWTs', 'Redis', 'ReactJs', 'Jenkins',
        <br />
        {"        "}
        'Kafka', 'Docker', 'Kubernetes'
        <br />
        {"      "}]
      </div>
      <div className={classes.methodContent}>{"}"}</div>
      <div className={classes.methodSpacing}>
        <span className={classes.punctuation}> </span>
        {"}"}
      </div>
      <div>{"}"}</div>
    </div>
  );
};

export default About;
