import React from "react";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles({
  skills: {
    display: "flex",
    flexWrap: "wrap",
    gap: "10px",
    marginTop: "10px",
  },
  skillBadge: {
    backgroundColor: "#0f2c1f",
    color: "#dfffe6",
    padding: "8px 14px",
    borderRadius: "8px",
    fontSize: "0.95rem",
    fontFamily: "monospace",
  },
  sectionTitle: {
    color: "#f38ba8",
    fontFamily: "monospace",
    fontSize: "16px",
    marginBottom: "10px",
    marginTop: "0.5rem",
  },
});

const SkillsList = () => {
  const classes = useStyles();

  const languages = [
    "Java",
    "Javascript",
    "SQL",
    "C",
    "C++",
    "Python",
    "HTML",
    "CSS",
  ];

  const toolsAndTechnologies = [
    "Spring(DI, AOP, Integration, MVC)",
    "Spring Boot",
    "Hibernate",
    "Struts",
    "Servlets",
    "GIT/GitHub",
    "AWS(SES, SNS, SQS, Pinpoint, S3, EC2, Open Search)",
    "Linux",
    "Microservices",
    "JWTs",
    "Redis",
    "ReactJs",
    "Jenkins",
    "Kafka",
    "Docker",
    "Kubernetes",
  ];

  return (
    <div>
      <div className={classes.sectionTitle}>Languages:</div>
      <div className={classes.skills}>
        {languages.map((skill) => (
          <span className={classes.skillBadge} key={skill}>
            {skill}
          </span>
        ))}
      </div>

      <div className={classes.sectionTitle}>Tools and Technologies:</div>
      <div className={classes.skills}>
        {toolsAndTechnologies.map((skill) => (
          <span className={classes.skillBadge} key={skill}>
            {skill}
          </span>
        ))}
      </div>
    </div>
  );
};

export default SkillsList;
