import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@mui/styles";
import { fetchProjectsStart } from "../redux/slices/projectSlice";

const useStyles = makeStyles({
  projectsContainer: {
    fontFamily: "monospace",
    color: "#cdd6f4",
  },
  projectCard: {
    backgroundColor: "#1e1e2e",
    border: "1px solid #313244",
    borderRadius: "8px",
    padding: "16px",
    marginBottom: "16px",
    display: "flex",
    gap: "16px",
  },
  leftColumn: {
    flex: "1",
    display: "flex",
    flexDirection: "column",
    gap: "12px",
  },
  rightColumn: {
    flex: "0 0 300px", // Fixed width for languages column
    display: "flex",
    flexDirection: "column",
    gap: "8px",
  },
  projectTitle: {
    color: "#f38ba8",
    fontSize: "18px",
    fontWeight: "bold",
    marginBottom: "8px",
  },
  projectDescription: {
    color: "#cdd6f4",
    fontSize: "14px",
    lineHeight: "1.5",
    marginBottom: "12px",
    flexGrow: 1,
  },
  projectUrl: {
    color: "#89b4fa",
    fontSize: "12px",
    textDecoration: "none",
    alignSelf: "flex-start",
    "&:hover": {
      textDecoration: "underline",
    },
  },
  languagesContainer: {
    display: "flex",
    flexWrap: "wrap",
    gap: "8px",
  },
  languageBadge: {
    backgroundColor: "#313244",
    color: "#a6e3a1",
    padding: "4px 8px",
    borderRadius: "4px",
    fontSize: "12px",
    fontFamily: "monospace",
  },
  languagesLabel: {
    color: "#f9e2af",
    fontSize: "14px",
    fontWeight: "bold",
    marginBottom: "8px",
  },
  loadingText: {
    color: "#89b4fa",
    fontSize: "14px",
    textAlign: "center",
    padding: "20px",
  },
  errorText: {
    color: "#f38ba8",
    fontSize: "14px",
    textAlign: "center",
    padding: "20px",
  },
  noProjectsText: {
    color: "#6c7086",
    fontSize: "14px",
    textAlign: "center",
    padding: "20px",
  },
  // Responsive design for smaller screens
  "@media (max-width: 768px)": {
    projectCard: {
      flexDirection: "column",
    },
    rightColumn: {
      flex: "1",
    },
  },
});

const Projects = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { projects, loading, error } = useSelector((state) => state.project);

  return (
    <div className={classes.projectsContainer}>
      {projects.map((project, index) => (
        <div key={project.url || index} className={classes.projectCard}>
          {/* Left Column - Project Info */}
          <div className={classes.leftColumn}>
            <div className={classes.projectTitle}>{project.name}</div>

            {project.description && (
              <div className={classes.projectDescription}>
                {project.description}
              </div>
            )}

            <a
              href={project.htmlUrl}
              target="_blank"
              rel="noopener noreferrer"
              className={classes.projectUrl}
            >
              View Source Code 
            </a>
          </div>

          {/* Right Column - Languages */}
          <div className={classes.rightColumn}>
            {project.languages && project.languages.length > 0 && (
              <>
                <div className={classes.languagesLabel}>Languages:</div>
                <div className={classes.languagesContainer}>
                  {project.languages.map((language, langIndex) => (
                    <span
                      key={`${project.name}-${language}-${langIndex}`}
                      className={classes.languageBadge}
                    >
                      {language}
                    </span>
                  ))}
                </div>
              </>
            )}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Projects;
